import React, { useState } from 'react';
import { 
  AppBar, Toolbar, Typography, Button, Container, Grid, Paper, TextField 
} from '@mui/material';
import { CloudUpload, NavigateBefore, NavigateNext } from '@mui/icons-material';
import ReactMarkdown from 'react-markdown';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { solarizedlight } from 'react-syntax-highlighter/dist/esm/styles/prism';
import Papa from 'papaparse';

const App = () => {
  const [data, setData] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    Papa.parse(file, {
      header: true,
      complete: (results) => {
        setData(results.data);
      },
    });
  };

  const handlePrevious = () => {
    setCurrentIndex((prev) => Math.max(0, prev - 1));
  };

  const handleNext = () => {
    setCurrentIndex((prev) => Math.min(data.length - 1, prev + 1));
  };

  const handleGoToRow = (event) => {
    const row = parseInt(event.target.value, 10);
    if (!isNaN(row) && row > 0 && row <= data.length) {
      setCurrentIndex(row - 1);
    }
  };

  const CodeBlock = ({ language, value }) => (
    <div style={{ position: 'relative' }}>
      <SyntaxHighlighter language={language} style={solarizedlight}>
        {value}
      </SyntaxHighlighter>
      <Button
        variant="contained"
        size="small"
        style={{ position: 'absolute', top: 5, right: 5 }}
        onClick={() => navigator.clipboard.writeText(value)}
      >
        Copy
      </Button>
    </div>
  );

  const currentItem = data[currentIndex] || {};

  return (
    <>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            <strong>OTS Coding Hybrid </strong>
            - SxS Mixtral vs v0_synthetic Eval
          </Typography>
          <Button color="inherit" component="label">
            Upload CSV
            <input type="file" hidden onChange={handleFileUpload} accept=".csv" />
            <CloudUpload sx={{ ml: 1 }} />
          </Button>
          <Button color="inherit" onClick={handlePrevious} disabled={currentIndex === 0}>
            <NavigateBefore />
          </Button>
          <TextField
            type="number"
            size="small"
            value={currentIndex + 1}
            onChange={handleGoToRow}
            sx={{ width: 70, mx: 1, input: { color: 'white' } }}
          />
          <Typography>/ {data.length}</Typography>
          <Button
            color="inherit"
            onClick={handleNext}
            disabled={currentIndex === data.length - 1}
          >
            <NavigateNext />
          </Button>
        </Toolbar>
      </AppBar>
      <Container maxWidth="lg" sx={{ mt: 4 }}>
        {currentItem.prompt && (
          <>
            <Paper sx={{ p: 2, mb: 2 }}>
              <ReactMarkdown>{currentItem.prompt}</ReactMarkdown>
            </Paper>
            <Grid container spacing={2}>
              <Grid item xs={6}>
                <Paper sx={{ p: 2 }}>
                  <Typography variant="h6" gutterBottom>
                    V0_Synthetic response (R1)
                  </Typography>
                  <ReactMarkdown
                    components={{
                      code: ({ node, inline, className, children, ...props }) => {
                        const match = /language-(\w+)/.exec(className || '');
                        return !inline && match ? (
                          <CodeBlock
                            language={match[1]}
                            value={String(children).replace(/\n$/, '')}
                            {...props}
                          />
                        ) : (
                          <code className={className} {...props}>
                            {children}
                          </code>
                        );
                      },
                    }}
                  >
                    {currentItem['V0_Synthetic response (R1)']}
                  </ReactMarkdown>
                </Paper>
              </Grid>
              <Grid item xs={6}>
                <Paper sx={{ p: 2 }}>
                  <Typography variant="h6" gutterBottom>
                    Mixtral 8x22B response (R2)
                  </Typography>
                  <ReactMarkdown
                    components={{
                      code: ({ node, inline, className, children, ...props }) => {
                        const match = /language-(\w+)/.exec(className || '');
                        return !inline && match ? (
                          <CodeBlock
                            language={match[1]}
                            value={String(children).replace(/\n$/, '')}
                            {...props}
                          />
                        ) : (
                          <code className={className} {...props}>
                            {children}
                          </code>
                        );
                      },
                    }}
                  >
                    {currentItem['Mixtral 8x22B response (R2)']}
                  </ReactMarkdown>
                </Paper>
              </Grid>
            </Grid>
          </>
        )}
      </Container>
    </>
  );
};

export default App;
